.home__row {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  flex-flow: wrap;
}

.home__container {
  width: 90%;
  margin: 35px auto;
}
@media screen and (max-width: 620px) {
  .home__container {
    width: 100%;
  }
}

.nav-bar {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 100;
  background-color: #0e141e;
  height: 4rem;
  display: flex;
  flex-wrap: wrap;
  justify-items: flex-start;
  justify-content: space-between;
  align-items: center;
}
.left-bar {
  display: flex;
}
.searchBox {
  display: flex;
  flex-grow: 1;
}
.search-form {
  flex-grow: 1;
  flex-direction: row;
}

.search-input {
  display: flex;
  flex-grow: 1;
  background: whitesmoke;
  align-items: center;
  margin-right: 1rem;
  height: 40px;
  border-radius: 50px;
  border: 2px solid #ffc107;
  outline: none;
  margin: 0 10px;
  padding: 0 10px;
}
.search-button {
  border: none;
  border-radius: 25px;
  text-align: center;
  padding: 0.45rem 0.8rem;
  outline: none;
  margin-right: 0.2rem;
  margin-bottom: 1rem;
  cursor: pointer;
  background: #ffc107;
  transition: 0.5s;
}
.right-bar {
  display: flex;
  padding: 8px 0px 8px 16px;
  list-style: none;
  height: 60px;
}
.right-bar a {
  text-decoration: none;
  color: #f5f5f5;
  font-size: 18px;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0 16px;
  border-radius: 4px;
}

.right-bar li a:hover {
  background-color: #ffc107;
}

.profile-content {
  width: 100%;
  background-color: #0e141e;
  border-radius: 10px;
  transition: 850ms;
}
.right-bar span {
  margin-left: 0.5rem;
}

.PersonOutlineIcon {
  color: #ffc107;
}
.VerifiedUserIcon {
  color: #20a020;
}
.menu-bars {
  margin-left: 1rem;
  font-size: 2rem;
  background: none;
}
.nav-brand a {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: 100%;
  display: flex;
  align-items: center;
  text-decoration: none;
  color: #f5f5f5;
  font-size: 20px;
  padding: 0 16px;
  border-radius: 4px;
}

/* hamburger */
.nav-menu {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 100;
  background-color: #0e141e;
  width: 250px;
  height: 100vh;
  display: flex;
  justify-content: center;
  position: fixed;
  top: 0;
  left: -100%;
  transition: 850ms;
}
.nav-menu.active {
  left: 0;
  transition: 350ms;
}
.nav-text {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 8px 0px 8px 16px;
  list-style: none;
  height: 60px;
}
.nav-text a {
  text-decoration: none;
  color: #f5f5f5;
  font-size: 18px;
  width: 95%;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0 16px;
  border-radius: 4px;
}
.nav-text a:hover {
  background-color: #ffc107;
}

.nav-menu-items {
  width: 100%;
}
.navbar-toggle {
  background-color: #0e141e;
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.nav-text span {
  margin-left: 0.5rem;
}

@media only screen and (max-width: 760px) {
  .nav-brand {
    display: none;
  }
  .searchBox {
    width: 60%;
    margin-right: -50px;
  }
  .search-input {
    margin: 0;
  }
  .search-button {
    margin-top: 7px;
    margin-left: -50px;
  }
}
@media only screen and (max-width: 1200px) {
  .nav-bar-profile {
    display: none;
  }
  .nav-bar-admin {
    display: none;
  }
}

