* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body,
input {
  font-family: "popins", sans-serif;
}

.dabba {
  position: relative;
  width: 100%;
  min-height: 100vh;
  background-color: #fff;
  overflow: hidden;
}
.dabba:before {
  content: "";
  position: absolute;
  width: 2000px;
  height: 2000px;
  border-radius: 50%;
  background: linear-gradient(-45deg, #4481eb, #04befe);
  top: -10%;
  right: 48%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  z-index: 6;
  transition: 1.8s ease-in-out;
}
.forms-dabba {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}
.signin-signup {
  position: absolute;
  top: 50%;
  left: 75%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 50%;
  display: grid;
  grid-template-columns: 1fr;
  z-index: 5;
  transition: 1s 0.7s ease-in-out;
}
form {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 0 5rem;
  overflow: hidden;
  grid-column: 1 / 2;
  grid-row: 1 / 2;
  transition: 0.2s 0.7s ease-in-out;
}
form.sign-in-form {
  z-index: 2;
}
form.sign-up-form {
  z-index: 1;
  opacity: 0;
}
.title {
  font-size: 2.2rem;
  color: #444;
  margin-bottom: 10px;
}
.input-field {
  max-width: 380px;
  width: 100%;
  height: 55px;
  background-color: #f0f0f0;
  margin: 10px 0;
  border-radius: 55px;
  display: grid;
  grid-template-columns: 15% 85%;
  padding: 0.4rem;
}
.icon {
  text-align: center;
  padding-top: 5px;
  padding-left: 10px;
  color: #acacac;
  font-size: 1.1rem;
}
.input-field input {
  background: none;
  outline: none;
  border: none;
  line-height: 1;
  font-weight: 530;
  font-size: 1.1rem;
  color: #333;
}
.input-field input::-webkit-input-placeholder {
  color: #aaa;
  font-weight: 400;
}
.input-field input::placeholder {
  color: #aaa;
  font-weight: 400;
}
.butn {
  width: 150px;
  height: 48px;
  border: none;
  outline: none;
  border-radius: 48px;
  cursor: pointer;
  background-color: #ffc107;
  color: #343a40;
  text-transform: uppercase;
  font-weight: 530;
  margin: 10px 0;
  transition: 0.5s;
}
.butn:hover {
  background-color: #007bff;
  color: #fff;
}
.social-text {
  padding: 0.7rem;
  font-size: 1rem;
}
.social-media {
  display: flex;
  justify-content: center;
}
.social-icon {
  height: 46px;
  width: 46px;
  border: 1px solid #333;
  margin: 0 0.45rem;
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  color: #333;
  font-size: 1.1rem;
  border-radius: 50%;
  transition: 0.3s;
}

.social-icon:hover {
  color: #ffc107;
  border-color: #ffc107;
}
.panels-dabba {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}
.panel {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: space-around;
  text-align: center;
  z-index: 7;
}
.left-panel {
  pointer-events: all;
  padding: 3rem 17% 2rem 12%;
}
.right-panel {
  pointer-events: none;
  padding: 3rem 12% 2rem 17%;
}
.panel .content {
  color: #fff;
  transition: 0.9s 0.6s ease-in-out;
}
.pannel h3 {
  font-weight: 600;
  line-height: 1;
  font-size: 1.5rem;
}
.panel p {
  font-size: 0.95rem;
  padding: 0.7rem 0;
}
.image {
  width: 100%;
  transition: 1.1s 0.4s ease-in-out;
}
.butn.transparent {
  margin: 0;
  background: none;
  border: 2px solid #fff;
  color: #fff;
  width: 130px;
  height: 41px;
  font-weight: 600;
  font-size: 0.8rem;
}
.right-panel .content,
.right-panel .image {
  -webkit-transform: translateX(800px);
          transform: translateX(800px);
}

/* animation */
.dabba.sign-up-mode::before {
  -webkit-transform: translate(100%, -50%);
          transform: translate(100%, -50%);
  right: 52%;
}
.dabba.sign-up-mode .left-panel .image,
.dabba.sign-up-mode .left-panel .content {
  -webkit-transform: translateX(-800px);
          transform: translateX(-800px);
}
.dabba.sign-up-mode .right-panel .content,
.dabba.sign-up-mode .right-panel .image {
  -webkit-transform: translateX(0px);
          transform: translateX(0px);
}
.dabba.sign-up-mode .left-panel {
  pointer-events: none;
}
.dabba.sign-up-mode .right-panel {
  pointer-events: all;
}
.dabba.sign-up-mode .signin-signup {
  left: 25%;
}
.dabba.sign-up-mode form.sign-in-form {
  z-index: 1;
  opacity: 0;
}
.dabba.sign-up-mode form.sign-up-form {
  z-index: 2;
  opacity: 1;
}
@media (max-width: 870px) {
  .dabba {
    min-height: 800px;
    height: 100vh;
  }
  .dabba:before {
    width: 1500px;
    height: 1500px;
    left: 30%;
    bottom: 68%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    right: initial;
    top: initial;
    transition: 2s ease-in-out;
  }
  .signin-signup {
    width: 100%;
    left: 50%;
    top: 95%;
    -webkit-transform: translate(-50%, -100%);
            transform: translate(-50%, -100%);
    transition: 1s 0.8s ease-in-out;
  }
  .panels-dabba {
    /* z-index: 10; */
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 2fr 1fr;
  }
  .panel {
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    padding: 2.5rem 8%;
  }
  .panel .content {
    padding-right: 15%;
    transition: 0.9s 0.8s ease-in-out;
  }
  .panel h3 {
    font-size: 1.2rem;
  }
  .panel p {
    font-size: 0.7rem;
    padding: 0.5rem 0;
  }
  .butn.transparent {
    width: 110px;
    height: 35px;
    font-size: 0.7rem;
  }
  .image {
    width: 200px;
    transition: 0.9s 0.6s ease-in-out;
  }
  .left-panel {
    grid-row: 1 / 2;
  }
  .right-panel {
    grid-row: 3 / 4;
  }
  .right-panel .content,
  .right-panel .image {
    -webkit-transform: translateY(300px);
            transform: translateY(300px);
  }
  .dabba.sign-up-mode::before {
    -webkit-transform: translate(-50%, 100%);
            transform: translate(-50%, 100%);
    bottom: 32%;
    right: initial;
  }
  .dabba.sign-up-mode .left-panel .image,
  .dabba.sign-up-mode .left-panel .content {
    -webkit-transform: translateY(-300px);
